@import "../variables.css";

.container {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;

  width: 100%;
  text-align: center;

  background-color: var(--backgroundColor);
  color: var(--darkTextColor);

  font-size: 16pt;
  padding: 10px;

  box-shadow: 0 -2px 10px 0 rgba(0,0,0,0.5);
}



